                bookcomment: '1Dr. Axel Rauschmayer2015102017X<br/>2ES6<br/>3&#9733;&#9733;&#9733;&#9733;&#9733;<br/>4555xxx<br/>5',
                bookcover: book_es6_jpg
            },{
                bookname_ch: 'https://dulinrain.github.io/books/Exploring ES2016 and ES2017.pdf',
                bookname_en: 'http://exploringjs.com/es2016-es2017/',
                bookcomment: '1Dr. Axel Rauschmayer201720177<br/>2ECMAScript2016ECMAScript2017<br/>3ECMAScript&#9733;&#9733;&#9733;&#9733;&#9733;<br/>461<br/>5',
