                bookname_en: 'https://dulinrain.github.io/books/TypeScript JavaScript Development Guide.pdf',
                bookcomment: '1Nicholas Brown201620177<br/>2TypeScript<br/>3&#9733;<br/>4TypeScript<br/>511443<br/>6',
